import { useState } from "react"
import { Link } from "react-router-dom"
import styled from "styled-components";

const Wrapper = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    
    input {
        margin: 3rem;
        font-size: 3rem;
        border: none;
        border-bottom: 0.3rem solid black;
        outline: none;
    }

    a {
        font-size: 3rem;
        color: black;
    }

    div {
        font-size: 3rem;
    }
`;

const Search = () => {

    const [ searched, setSearched ] = useState([])

    function searchUsers(e) {
        const searchterm = e.target.value
  
        fetch("/api/search_users", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ searchterm })
        })
        .then(res => res.json())
        .then(res => setSearched(res))
        .catch(err => console.error(err))
    }

    return ( 
        <Wrapper> 
            <input placeholder="Search users..." type="search" onChange={searchUsers} />

            {searched && !searched[0]?.nousers ? searched.map(user => {
                return (
                    <Link key={user.user_id}
                          to={{pathname:`/userprofile/${user.username}`}}
                    >
                        {user.username}
                    </Link>
                )
            })

            :

            <div>
                {searched[0].nousers}
            </div>
        }
        </Wrapper>
    )
}

export default Search;
