import { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import styled from "styled-components";
import { io } from "socket.io-client"

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  font-size: 2.5rem;
  flex: 1;
  align-items: stretch;
  min-height: 100vh;

	* {
    	font-size: 2.1rem;
		  display: flex;
  	}
`;

const Title = styled.div`
  background-color: lightgray;
  position: sticky;
  top: 0;
  word-break: break-all;
  justify-content: space-between;
`;

const Chat = styled.div`
	flex-direction: column;
  padding-top: 1rem;
`;

const Chatdiv = styled.div`
  flex:1;
`;

const Rightmess = styled.div`
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  margin-left: auto;
  color: white;
  border-radius: 1rem;
  padding: 0.5rem;
  max-width: 60%;
  background: #4B5C77;
  word-break: break-all;
`;

const Leftmess = styled.div`
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  max-width: 60%;
  color: black;
  border-radius: 1rem;
  padding: 0.5rem;
  max-width: 60%;
  background: #acb0ad;
  word-break: break-all;
`;

const Formstyled = styled.form`
  margin-top: auto;
  position: sticky;
  bottom: 0;

  input[type=text] {
    border-radius: 1rem;
    width: 100%;
  }
`;

const Chatbox = () => {

  let history = useHistory()

  const socket = io.connect()

  const { id } = useParams()

  const sendername = localStorage.getItem("user");

  const [messages, setMessages] = useState([]);
  const [messageinfo, setMessageinfo] = useState({ username:id, msg:"", sendername });
  const { username, msg } = messageinfo;

  useEffect(() => {
    const info = { id, sendername }
    fetch("/api/get_chat", {
      method: "POST",
      mode: "cors",
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(info)
    })
    .then(res => res.json())
    .then(res => setMessages(res))

  }, [])

  useEffect(() => {
    socket.on("message", message => {
       if (message.sendername == id || sendername)
         setMessages(prev => [...prev, message])
       })
  }, [])

  function sendMessage(e) {
    e.preventDefault()
    socket.emit("send_chat_message", messageinfo)
    setMessages(prev => [...prev, messageinfo])
  }

  function handleInput(e) {
    setMessageinfo((prevInfo) => ({ ...prevInfo, msg: e.target.value }))
  }

  function goBack() {
    history.goBack()
  }

  return (
    <Wrapper>
            <Title>You are chatting with {username} <button onClick={goBack}>Back</button></Title>

			      <Chat>
                  	{messages.length ? messages.map(message => {
                  	    return (
                  	      	<Chatdiv key={message.id}>
                  	      		{sendername == message.sendername ? 

                  	      		<Rightmess>{message.msg}</Rightmess>
                       
                  	      		: 

                              <Leftmess>{message.msg}</Leftmess>
                  	      		           
                  	      		}
                  	      	</Chatdiv>
                  	    )
                  	})	
                  
                  	:
                  
                  	<div>
                  	  No chat messages...
                  	</div>
                  	}
			      </Chat>
            <Formstyled onSubmit={sendMessage}>
              <input type="text" placeholder="Write a message..." name="msg" value={msg} onChange={handleInput} />
              <input type="submit" className="fas fa-arrow-right"/>
            </Formstyled>
    </Wrapper>
  )
};

export default Chatbox;
