import { useState, useEffect } from "react"

export default function useFetchGet(url) {
    const [ data, setData ] = useState()

    function getDataFromServer(url) {
        try {
            fetch(url)
            .then(res => res.json())
            .then(res => setData(res))
        } catch(err){
            console.error(err)
        }
    }

    useEffect(() => getDataFromServer(url), [])

    return data
};
