import { useState, useEffect } from "react"
import { useParams } from "react-router-dom"
import styled from "styled-components";
import PostVote from "./PostComponents/Vote"
import PostContent from "./PostComponents/Content";
import EditPost from "./PostComponents/Edit";

const Wrapper = styled.div`
    display: flex;
    font-size: 4rem;
    word-break: break-word;
    flex-direction: column;
`;

const Post = styled.div`
    flex: 1;
    display: flex;
    margin: 1rem;
    border-bottom: solid 0.4rem rgb(190, 194, 192);
`;

const Loading = styled.div`
    font-size: 3rem;
`;

const Postdetails = () => {

    const loggedInUser = localStorage.getItem("user");

    const { id } = useParams();
    const post_id = id

    const [ isedit, setIsedit ] = useState(false)
    const [ postinfo, setPostinfo ] = useState();

    async function fetchSend(url, request, body_data) {
        try {
            const data = await fetch(url, {
                method: request,
                mode: "cors",
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify(body_data)
            })      
            return await data.json();

        } catch(err){
            console.error(err)
        }
    }
    
    useEffect(() => {
        async function fetchData() {
            const result = await fetchSend("/api/post_info", "POST", { post_id })
            setPostinfo(result)
        }
        fetchData()
    }, [post_id])

    function sendEdit() {
        fetchSend("/api/edit_post", "POST", postinfo)
        setPostinfo({ ...postinfo })
        setIsedit(false)
    }

    function deletePost() {
        fetchSend("/api/delete_post", "POST", { post_id })
        window.location.href = "/"
    }
 
    function votePost(e) {
        const islike = e.target.getAttribute("name")
        fetchSend("/api/vote_post", "PUT", { post_id, islike })
        renderVotechange(islike)
    }
    
    function renderVotechange(islike) {

        if (islike == "true") islike = true
        else islike = false

        if (postinfo.liked == islike) {
            if (postinfo.liked) postinfo.post_votes -= 1
            else postinfo.post_votes += 1

            postinfo.liked = null
        }
        else {
            if (postinfo.liked == null && islike) postinfo.post_votes += 1
            else if (postinfo.liked == null && !islike) postinfo.post_votes -= 1
            else {
                if (postinfo.liked != null && islike) postinfo.post_votes += 2
                else if (postinfo.liked != null && !islike) postinfo.post_votes -= 2
            }     

            postinfo.liked = islike
        }
      
        setPostinfo({ ...postinfo })
    }

    function copyClip() {
        navigator.clipboard.writeText(window.location.href) 
    }

    function handleInput(e) { 
        setPostinfo(prevInfo => ({ ...prevInfo, post_content: e.target.value }))
    }

    function setEdit() { 
        setIsedit(!isedit) 
    }

    return (
        <Wrapper>
 
            {postinfo ? isedit && postinfo ?
            <EditPost 
                postinfo={postinfo}
                setEdit={setEdit}
                sendEdit={sendEdit}
                handleInput={handleInput}
            />

            : 

            <Post>
                <PostVote 
                    votePost={votePost}
                    postinfo={postinfo}
                />
                <PostContent
                    postinfo={postinfo}
                    copyClip={copyClip}
                    deletePost={loggedInUser === postinfo.username && deletePost}
                    setEdit={loggedInUser === postinfo.username && setEdit}
                />
            </Post>

            :

            <Loading>
                LOADING...
            </Loading>
            }  
        </Wrapper>
    )
}

export default Postdetails;
