import CommentReply from "./CommentComponents/Reply"
import CommentVote from "./CommentComponents/Vote"
import CommentContent from "./CommentComponents/Content"
import CommentNew from "./CommentComponents/New"
import CommentEdit from "./CommentComponents/Edit"
import styled from "styled-components"

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
`;

const Maindiv = styled.div`
    display: flex;
`;

const Commentwrap = styled.div`
    display: flex;
    font-size: 2rem;
    flex-direction: row;
    border: 0.1rem solid rgb(179, 175, 175);
    border-bottom-right-radius: 5rem;
    margin: 2rem;
    ${({ active }) => active && `border: 0.5rem solid rgb(2, 2, 2);`};
`;

const Commentreplywrap = styled.div`
    display: flex;
    font-size: 2rem;
    width: 100%;
    flex-direction: column;
`;

const Nocomments = styled.div`
    font-size: 3rem;
    margin: 2rem;
`;

const CommentRender = (props) => {

    const { createComment, handleInput, newcommentinfo, commentsinfo, sendEdit, handleEdit, 
            handleInputEdit, voteComment, deleteComment, copyClip, openReply, post_id,
            createReply, handleInputReply, openReplyArray, iseditid, user, comment_id
    } = props

    return (
        <Wrapper>
            <CommentNew 
                createComment={createComment}
                handleInput={handleInput}
                newcommentinfo={newcommentinfo}
            />
            {commentsinfo?.length ? commentsinfo.map((comment => {
                return (
                    <Maindiv key={comment.id}>             
                        {comment.id == iseditid ?
                        <CommentEdit 
                            sendEdit={sendEdit}
                            handleEdit={handleEdit}
                            handleInputEdit={handleInputEdit}
                        />

                        :

                        <Commentreplywrap>
                            <Commentwrap active={comment.id == comment_id}>
                                <CommentVote 
                                    comment={comment}
                                    voteComment={voteComment}
                                />
                                <CommentContent 
                                    comment={comment}
                                    openReply={openReply}
                                    post_id={post_id}
                                    copyClip={copyClip}
                                    handleEdit={comment.username === user && handleEdit}
                                    deleteComment={comment.username === user && deleteComment}
                                />
                            </Commentwrap>
                            {openReplyArray == comment.id &&
                            <CommentReply
                                handleInputReply={handleInputReply}
                                comment_id={comment.id}
                                createReply={createReply}
                                openReply={openReply}
                            />
                            }
                        </Commentreplywrap>              
                        }
                    </Maindiv>
                )
            }))
            :
            <Nocomments>
                Be the first to comment ...
            </Nocomments>
            }
        </Wrapper>
    )
}

export default CommentRender;
